import {SliderControls} from './slider-controls';

class Slider3D {
  constructor() {
    this.slider3d = document.querySelector(`.slider-3d`);
    if (!this.slider3d) {
      return;
    }

    this._currentSlideNum = 0;
    this.slides = Array.from(this.slider3d.querySelectorAll(`.slider-3d__slide`))
    this.SliderControls = new SliderControls({
      slider: this,
      controlsBlock: this.slider3d.querySelector(`.slider-nav`),
    });

    this.arrangeSlides();
  }

  arrangeSlides() {
    const slidesCount = this.slides.length;
    this.slides.forEach((slide, index) => {
      const slidePosition = (slidesCount + index - this.current) % slidesCount;

      slide.style.zIndex = `${slidesCount - slidePosition}`;
      slide.style.transition = `all 0.25s ease-out`;

      if (slidePosition === slidesCount - 1) {
        slide.style.transition = `opacity 0.25s ease-out`;
        // slide.style.transition = `none`;
        slide.style.opacity = 0;
      }
      
      if (slidePosition === 0) {
        // slide.style.transition = `all 0.25s ease-out`;
        slide.style.opacity = 0;
        slide.style.transform = `perspective(800px) translate3d(-100px, 0, 100px)`;
      } else {
        slide.style.transform = `perspective(800px) translate3d(${slidePosition * 30}px, 0, ${-40 * slidePosition}px)`;
        slide.style.opacity = `${1.25 - 0.25 * slidePosition}`;
      }

    });
  }

  set current(value) {
    this._currentSlideNum = value;
    this.arrangeSlides();
  }

  get current() {
    return this._currentSlideNum;
  }
}

export {Slider3D};
